<template>
  <!-- tabs 组件封装 -->
  <div class="tab-list">
    <span
      :class="['item', activeIndex === item.value ? 'active' : '']"
      v-for="(item, index) in list"
      :key="index"
      @click="tabItem(index, item)"
      >{{ item.label }}</span
    >
  </div>
</template>

<script lang="ts" setup>
defineProps<{
  list: Array<any>;
  activeIndex: number | string;
}>();
const emits = defineEmits<{
  (e: "emitTab", value: { index: number; item: Record<string, any> }): void;
}>();
const tabItem = (index, item) => {
  emits("emitTab", item);
};
</script>
<style lang="scss" scoped>
.tab-list {
  display: flex;
  align-items: center;
  height: 36px;
  user-select: none;
  .item {
    font-size: 16px;
    color: #242934;
    line-height: 1;
    margin-right: 48px;
    cursor: pointer;
    &.active {
      color: #0056ff;
      font-weight: 800;
    }
    &:last-child {
      margin-right: 0;
    }
  }
}
</style>
